import { getAllData } from './data.js'
import { Sign, isBasic } from './utils.js'

const data = getAllData()
export async function getContent(type: string) {
  let content = data[type]
  if (!isBasic(type)) {
    const basic = await getBasic()
    content = basic.concat(content)
  }
  content.unshift(Sign())
  return content.join('\n')
}

export async function getBasic() {
  return data["basic"]
}
